import React, { useState } from 'react'
import '../../src/App.css';
import './NFTSales.css';
import { compactAddress } from '../helpers/utils';
import "react-toastify/dist/ReactToastify.css";
import {ToastContainer, toast} from "react-toastify";

function NFTSales({chain, settings}) {

  const [contract, setContract] = useState('');
  const [marketPlace, setMarketPlace] = useState('');
  const [tokenId, setTokenId] = useState('');
  const [seller, setSeller] = useState('');
  const [buyer, setBuyer] = useState('');
  const [listNFTSales, setListNFTSales] = useState([])

  function returnChosedChain() {
    let chosedChain;
    if (chain === "Ethereum"){
      chosedChain = "eth-mainnet"
    }else{
      chosedChain = "matic-mainnet"
    }
    return chosedChain;
  }

  function toastMessage(text) {
    toast.info(text)  ;
  }

  async function getSales(settings){
    const options = {method: 'GET', headers: {accept: 'application/json'}};
    
    let queryString = `https://${returnChosedChain()}.g.alchemy.com/nft/v2/${settings.apiKey}/getNFTSales?fromBlock=0&toBlock=latest&order=desc`
    if(contract !== ``){
        queryString = queryString + `&contractAddress=${contract}`
    }
    if(tokenId !== ``){
        queryString = queryString + `&tokenId=${tokenId}`
    }

    if(buyer !== ``){
        queryString = queryString + `&buyerAddress=${buyer}`
    }

    if(seller !== ``){
        queryString = queryString + `&sellerAddress=${seller}`
    }
    fetch(queryString, options)
      .then(response => response.json())
      .then(response => 
        {
            console.log(response);
            setListNFTSales(response.nftSales)
        })
      .catch(err => toastMessage('Invalid params'));
  }
//   https://eth-mainnet.g.alchemy.com/nft/v2/8Srm8dlSQID3Dtw2xVb74bTD-Vw3_L51/getNFTSales?fromBlock=0&toBlock=latest&order=asc&contractAddress=0x0e5b1348bddc08b24efb3cca4f2a519da305cd4f&tokenId=3628&buyerAddress=senra.eth&sellerAddress=0xCC9C183aFcd723aA6c5E5b0219D62c99A55169E0

  return (
    <div className='box-container'>
        <ToastContainer position="top-center" autoClose={5000}/>
        <div className='grid-filter'>

            <label>Contract</label>
            <input type="text" onChange={(e) => setContract(e.target.value)} value={contract}/>

            <label>Token Id</label>
            <input type="number" onChange={(e) => setTokenId(e.target.value)} value={tokenId}/>

            <label>Buyer address</label>
            <input type="text" onChange={(e) => setBuyer(e.target.value)} value={buyer}/>

            <label>Seller address</label>
            <input type="text" onChange={(e) => setSeller(e.target.value)} value={seller}/>

        </div>
        <button className='button' onClick={() =>getSales(settings)}>Get sales</button>

        {
            listNFTSales && 
            <>
                <hr/>
                <h3>Sales</h3>
                {listNFTSales.length > 0 &&
                    <label>Sales made: {listNFTSales.length}</label>
                }
                
                <table className='table'>
                <thead>
                    <tr>
                    <td>
                        Contract
                    </td>
                    <td>
                        TokenId
                    </td>
                    <td>
                        Buyer
                    </td>
                    <td>
                        Seller
                    </td>
                    <td>
                        Marketplace
                    </td>
                    <td>
                        Value
                    </td>
                    <td>
                        Link OpenSea
                    </td>
                    </tr>
                </thead>
                <tbody>
                    {listNFTSales && listNFTSales.map((nft, ind) => 
                    <tr key={ind}>
                        <td>{ compactAddress( nft.contractAddress)}</td>
                        <td>{ compactAddress( nft.tokenId)}</td>
                        <td>{ compactAddress( nft.buyerAddress)}</td>
                        <td>{ compactAddress( nft.sellerAddress)}</td>
                        <td>{nft.marketplace}</td>
                        <td>{(nft.sellerFee.amount   )/ 1000000000000000000}</td>
                        <td>
                            <a href={`https://opensea.io/assets/${chain.toLowerCase()}/${nft.contractAddress}/${nft.tokenId}`} target='_blank'>See</a>
                        </td>
                    </tr>
                    )}
                </tbody>
                </table>
            </>
        }

    </div>
  )
}

export default NFTSales